import React from 'react';
import DualListBox from 'react-dual-listbox';

const options = [
    { value: 'Admin', label: 'Admin' },
    { value: 'Agent_admin', label: 'Agent_admin' },
    { value: 'Approval_admin', label: 'Approval_admin' },
    { value: 'Approve_user', label: 'Approve_user' },
    { value: 'Assignment_rule_admin', label: 'Assignment_rule_admin' },
    { value: 'Integration_scenario_designer', label: 'Integration_scenario_designer' },
    { value: 'Integration_scenario_monitor', label: 'Integration_scenario_monitor' },
    { value: 'Workflow_scenario_designer', label: 'Workflow_scenario_designer' },
    { value: 'Workflow_scenario_monitor', label: 'Workflow_scenario_monitor' },
];

class BasicExample extends React.Component {
    constructor() {
        super();

        this.state = { selected: ['Admin', 'Agent_admin'] };

        this.onChange = this.onChange.bind(this);
    }

    onChange(selected) {
        this.setState({ selected });
    }

    render() {
        const { selected } = this.state;

        return <DualListBox options={options} selected={selected} onChange={this.onChange} />;
    }
}

export default BasicExample;
